import axios from 'axios';
import { createStore } from 'vuex'

const store = createStore({
  state(){
    return {
      name : 'nam',
      age: 31,
      likes: 30,
      좋아요눌렀니: false,
      more : {},
    }
  },
  mutations: {
    setMore(state, data) {
      state.more = data
    },
    함수() {
      axios.get().then()
    },
    좋아요(state) {
      if (state.좋아요눌렀니 == false) {
        state.likes++;
        state.좋아요눌렀니 = true;
      } else {
        state.likes--;
        state.좋아요눌렀니 = false;
      }
    },
    증가(state) {
      state.age++
    },
    이름변경(state) {
      state.name = 'park'
    },
  },
  actions: {
    getDate(context){
      axios.get('https://codingapple1.github.io/vue/more0.json').then((a) => {
        console.log(a.data)
        context.commit('setMore', a.data)
      })
    },
  },
})

export default store